
  import {message} from 'ant-design-vue';
  import {defineComponent, ref} from 'vue';
  import {EffectsContext} from '../../../../../types';
  import {registerAntdMobile, SchemaFormEvents} from '../../../../schema-form';
  import effects1 from '../../../../views/demo/schema/effects1';
  import effects2 from '../../../../views/demo/schema/effects2';

  registerAntdMobile();

  export default defineComponent({
    name: 'Demo',
    setup() {
      const value3 = ref({});
      const value2 = ref([{
        basic: {
          bb: 'a',
          gg: 'fffff'
        },
        dd: [{
          ee: '是',
          ff: '是'
        }]
      }, {
        basic: {
          bb: 'c',
          cc: '1',
          gg: '1111'
        },
        dd: [{
          ee: '否',
          ff: '是'
        }]
      }]);
      const value = ref({
        f1: {
          aa: true,
          cc: true
        }
      });
      const actions = [{
        text: '提交',
        props: {type: 'primary'},
        action: ($) => {
          console.log($);
        }
      }, {
        text: '重置',
        action: () => {
          console.log('reset')
        }
      }, {
        text: '切换MM',
        action: ($) => {
          $('kk.mm').toggle();
        }
      }, {
        text: '校验',
        action: ($: EffectsContext) => {
          $.validate((errors) => {
            message.error(errors);
          });
        }
      }];
      return {
        value3,
        effects3: ($: EffectsContext) => {
          $('text').subscribe(SchemaFormEvents.fieldKeydown, (e) => {
            console.log(e);
          }).onFieldCreateOrChange((v) => {
            console.log(v);
          });
        },
        definition3: {
          fields: {
            text: {
              type: 'string',
              title: 'abc'
            }
          }
        },
        value2,
        value,
        actions,
        definition: effects1,
        effects: ($: EffectsContext) => {
          $('f1.aa').onFieldCreateOrChange((value) => {
            if (value) {
              $('f1.bb').hide();
            } else {
              $('f1.bb').show();
            }
          });
          $('f1.cc').onFieldCreateOrChange((value) => {
            if (value) {
              $('$card2').hide();
            } else {
              $('$card2').show();
            }
          });
        },
        effects2: ($) => {
          const cb1 = (value, path) => {
            const updatePath = path.substr(0, path.indexOf('.')) + '.basic.cc';
            if (value === 'a') {
              $(updatePath).setFieldProps({loading: true});
              setTimeout(() => {
                $(updatePath).setEnum(['1']);
                $(updatePath).setFieldProps({loading: false});
              }, 1000);
            } else if (value === 'b') {
              $(updatePath).setFieldProps({loading: true});
              setTimeout(() => {
                $(updatePath).setEnum(['1', '2']);
                $(updatePath).setFieldProps({loading: false});
              }, 1000);
            }
          };
          $('?.basic.bb').onFieldCreateOrChange(cb1);
          const cb2 = (value, path) => {
            if (value === '是') {
              $(path).takePath(1).appendPath('basic.gg').hide();
            } else if (value === '否') {
              $(path).takePath(1).appendPath('basic.gg').show();
            }
          };
          $('?.dd.?.ee').onFieldCreateOrChange(cb2);
          $('?.dd.?.ff').onFieldCreateOrChange((value, path) => {
            if (value === '是') {
              $(path).takePath(3).appendPath('ee').hide();
            } else if (value === '否') {
              $(path).takePath(3).appendPath('ee').show();
            }
          });
          $('?.basic.cc').setEnum([{
            label: '1111', value: '1111'
          }, {
            label: '2222', value: '2222'
          }]);
        },
        schema2: effects2
      };
    }
  });
